// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import Router from 'vue-router'
/* ------------------------------------------------------------------ */
// 引入字体
import '../src/assets/css/font.css'
// 引入动画
import 'animate.css/animate.min.css'
import 'hover.css/css/hover-min.css'
// 引入 element-ui
// eslint-disable-next-line import/no-duplicates
// eslint-disable-next-line import/no-duplicates
import ElementUI, {Notification} from 'element-ui'
// 引入 element-ui 的 css 文件
import 'element-ui/lib/theme-chalk/index.css'
// 引入axios
import axios from 'axios'
// 引入qs
import qs from 'qs'

import VueCookie from 'vue-cookie'
// 这是生成的自定义 css 文件
// import 'element-ui/lib/element-#FFA640/index.css'
// 引入高德地图组件
// import VueAMap from 'vue-amap';
// Vue.use(VueAMap);
/*
* ------------------------------------------------------------------
* 解决 chrome 报错
* Navigating to current location ("/zufang/sz") is not allowed
*/
const originalPush = Router.prototype.push
Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.config.productionTip = false

Vue.prototype.$qs = qs

Vue.prototype.$http = axios
// 加载组件
Vue.use(ElementUI)
// Tell Vue to use the plugin
Vue.use(VueCookie)

// 设置请求 api 地址
// axios.defaults.baseURL = 'http://121.36.29.50:9999/'
axios.defaults.retry = 4
axios.defaults.retryDelay = 1000

// 请求拦截器
axios.interceptors.request.use(config => {
  return config
  // eslint-disable-next-line handle-callback-err
}, err => {
  Notification.error({message: '请求超时!'})
})

// axios.interceptors.request.use(function (config) {
//   return config
// }, function (error) {
//   console.log(error)
// })
// 响应拦截器
axios.interceptors.response.use(data => {
  console.log('拦截器')
  console.log(data)
  if (data) {
  // 如果请求发送成功，并返回了200，但是服务器返回的JSON中的状态码是在错误码之内
    const statusCode = [4001, 4002, 4003, 4004, 4005, 4101, 4102, 4103, 4201, 4301, 4302, 4401, 4402, 4403, 5000]
    if (data.status && data.status === 200 && statusCode.indexOf(data.data.errorCode) !== -1) {
      console.log('服务器返回错误状态码,已被拦截')
      console.log(data)
      if (data.data.errorMessage) {
        Notification.error({title: data.data.errorCode, message: data.data.errorMessage})
      }
      return
    }
    return data
  } else {
    Notification.error({
      title: '500',
      message: '服务器错误'
    })
  }
  // eslint-disable-next-line handle-callback-err
}, err => {
  Notification.error({title: 'err', message: err})
  // if (err.response.status === 4001) {
  //   Message.error({message: '用户已存在'})
  // } else if (err.response.status === 4002) {
  //   Message.error({message: '该手机号已被注册'})
  // } else if (err.response.status === 4003) {
  //   Message.error({message: '验证码错误'})
  // } else if (err.response.status === 4004) {
  //   Message.error({message: '注册失败'})
  // } else if (err.response.status === 4005) {
  //   Message.error({message: '验证码发送失败'})
  // } else if (err.response.status === 4101) {
  //   Message.error({message: '该用户不存在，请重新输入'})
  // } else if (err.response.status === 4102) {
  //   Message.error({message: '用户名和密码不匹配'})
  // } else if (err.response.status === 4103) {
  //   Message.error({message: '暂未登录'})
  // } else if (err.response.status === 4201) {
  //   Message.error({message: '收藏失败'})
  // } else {
  //   if (err.response.data.msg) {
  //     Message.error({message: err.response.data.msg})
  //   } else {
  //     Message.error({message: '未知错误!'})
  //   }
  // }
})

// eslint-disable-next-line no-new
new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>'
})



