<template>
    <div>
        <el-container style="position:relative;margin: 0; padding: 0;border: 0;">
            <!--背景图片-->
            <el-row>
                <el-col :span="24" :push="0" :class="`${searchResult?'animated fadeOutUp slow':''}`">
                    <el-image style="width: 100%; height: 100%;opacity: 1"
                              src="../../static/img/homeImg.jpg"
                              fit="fill" />
                </el-col>
            </el-row>
            <!--搜索框-->
            <el-row style="width: 100%;position: absolute;">
                <el-input placeholder="" v-model="inputText"
                          @focus="gotoSZ"
                          @keyup.enter.native="searchByKeyWord"
                          :class="`input-with-select ${searchResult?'searchResultInput':'searchInput'}`"
                          style="width: 50%;height: 55px;"
                          type="text"
                          :clearable="true"
                          size="large">
                    <!--选择城市-->
                    <el-select v-model="RentalOptions.selectCityName.cityCnName" slot="prepend" placeholder="深圳市"
                               popper-class="selectOptions"
                               filterable
                               @change="changeCity">
                        <el-option
                                v-for="cityName in cityNames"
                                :key="cityName.cityId"
                                :value="cityName.cityPyName"
                                :label="cityName.cityCnName">
                            <span style="float: left">{{ cityName.cityCnName }}</span>
                            <span style="float: right; color: #8492a6; font-size: 15px">{{ cityName.cityPyName }}</span>
                        </el-option>
                    </el-select>

                    <el-button-group slot="append">
                        <!--            <el-button icon="el-icon-location-information" @click="$router.push('/map')">地图找房</el-button>-->
                        <!--            <el-button icon="el-icon-search" @click="searchResults"/>-->
                        <el-button icon="el-icon-search" @click="searchByKeyWord" />
                    </el-button-group>
                </el-input>
            </el-row>
            <!--筛选选项-->
            <el-row v-if="searchResult"
                    style="position: absolute;width: 100%;margin-top: 7%;"
                    class="animated fadeInUp slow el-container">
                <el-col :span="7" :push="5">
                    <el-cascader-panel
                            @change="changeCityRegion"
                            :options="RentalOptions.areaOptions"
                            :props="RentalOptions.props">
                    </el-cascader-panel>
                </el-col>
                <!--右半部分筛选框-->
                <el-col :span="7" :push="5">
                    <div style="border: 1px #E9EEF3 solid;width: 100%;height: 100%;">
                        <el-container style="width: 100%;height: 100%;display: flex;flex-direction: column;">
                            <!--类型-->
                            <el-row style="margin-top: 1%">
                                <span class="el-col-3" style="">类型</span>
                                <el-radio-group style="float:left" v-model="RentalOptions.typeOfRental">
                                    <el-radio-button label="整租"></el-radio-button>
                                    <el-radio-button label="合租"></el-radio-button>
                                </el-radio-group>
                            </el-row>
                            <!--朝向-->
                            <el-row style="margin-top: 1%">
                                <span class="el-col-3" style="">朝向</span>
                                <div>
                                    <el-radio-group style="float:left" v-model="RentalOptions.directionOfHouse">
                                        <el-radio-button label="东"></el-radio-button>
                                        <el-radio-button label="南"></el-radio-button>
                                        <el-radio-button label="西"></el-radio-button>
                                        <el-radio-button label="北"></el-radio-button>
                                    </el-radio-group>
                                </div>
                            </el-row>
                            <!--价格区间-->
                            <el-row style="margin-top: 1%">
                                <span class="el-col-3" style="">价格</span>
                                <el-col :span="19">
                                    <div>
                                        <el-radio-group style="float:left" v-model="RentalOptions.priceSelected">
                                            <el-radio-button
                                                    style="float:left"
                                                    v-for="price in RentalOptions.priceRange"
                                                    :label="price"
                                                    :key="price"></el-radio-button>
                                        </el-radio-group>
                                    </div>
                                </el-col>
                            </el-row>
                        </el-container>
                    </div>
                </el-col>
            </el-row>
            <!--搜索结果-->
            <el-row v-if="searchResult"
                    style="width: 100%;position: absolute;z-index: 0;margin-top: 18%" class="animated fadeInUp slow">
                <div style="display:flex;flex-direction: column;">
                    <el-row type="flex" justify="center" class="row-bg">
                        <p v-show="zufangDataLength===0">没有搜索结果哦,换个关键词或者添加筛选条件试试?</p>
                    </el-row>
                    <!--<p style="font-family: DengXian_Reg,serif;font-weight: bold;font-size: xx-large;text-align: center">推荐房源</p>-->
                    <!--根据总数据长度决定有多少行-->
                    <el-row type="flex" justify="center" class="row-bg"
                            v-for="(houses) in parseInt(zufangDataLength/3)+1"
                            :key="houses"
                            align="middle"
                            style="margin-top: 2%">
                        <!--每行三个数据-->
                        <el-col v-if="index < zufangDataLength" :span="5" v-for="(item, index) in 3" :key="item"
                                style="margin-left: 2%;margin-right: 2%">
                            <el-card :body-style="{ padding: '0px' }">
                                <img src="../../static/img/test_1.jpg" class="image hvr-grow" alt="" @click="$router.push($route.path+'/housedetail/'+zufangData[index].id)">
                                <div style="padding-top:14px; padding-bottom: 14px; width: 100%;text-align: center;"
                                     @click="$router.push('/housedetail/'+zufangData[index].id)"
                                     class="hvr-sweep-to-top">
                                    <span>{{zufangData[index].name}}</span>
                                    <div class="bottom clearfix">
                                        <time class="time">{{zufangData[index].price}}<br /></time>
                                    </div>
                                </div>
                            </el-card>
                            <!---->
                        </el-col>
                    </el-row>
                </div>
            </el-row>
        </el-container>
        <Footer :class="searchResult?'animated fadeOutUp slow':''" />
    </div>
</template>

<script>
import Footer from './Footer'
import Axios from 'axios'

export default {
  name: 'home',
  data: function () {
    return {
      activeIndex: 1,
      inputText: '',
      cityNames: [],
      zufangData: [],
      zufangDataLength: 0,
      /*
      * 用户点击搜索后,将此项变为 true,从而激发动画
      */
      searchResult: false,
      /*
      * 租房筛选选项
      */
      RentalOptions: {
        regionId: '',
        subwayStationId: '',
        // 用户选中的城市
        selectCityName: {
          cityCnName: '深圳市',
          cityId: '440300',
          cityPyName: 'sz'
        },
        // 在这里写用户选择的区域或者地铁
        props: {
          lazy: true,
          lazyLoad (node, resolve) {
            const nodes = []
            console.log(node)
            if (node.path[0].split('_')[3] === '区域' && node.path.length === 1) {
              // console.log('请求城市区域开始')
              /*
                                 * 请求对应城市的区域;
                                 */
              const urlRegion = '/region/' + encodeURIComponent(node.path[0].split('_')[0])
              Axios.get(urlRegion, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].regionId,
                        label: response.data.data[i].regionCnName,
                        // 是否叶子结点
                        leaf: true
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            } else if (node.path[0].split('_')[3] === '地铁' && node.path.length === 1) {
              // console.log('请求城市地铁开始')
              /*
                                 * 请求对应城市的地铁;
                                 */
              const urlSubway = '/subwayline/' + node.path[0].split('_')[2]

              Axios.get(urlSubway, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].lineId,
                        label: response.data.data[i].lineCnName,
                        // 是否叶子结点
                        leaf: false
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            } else if (node.path[0].split('_')[3] === '地铁' && node.path.length === 2) {
              /*
                                 * 请求对应地铁线路的地铁站台;
                                 */
              const urlSubwayStation = '/subwaystation/' + node.path[1]
              Axios.get(urlSubwayStation, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].stationId,
                        label: response.data.data[i].stationCnName,
                        // 是否叶子结点
                        leaf: true
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            }
          }
        },
        areaOptions: [
          {
            value: '深圳市_sz_440300_区域',
            label: '区域',
            children: []
          },
          {
            value: '深圳市_sz_440300_地铁',
            label: '地铁',
            children: []
          }
        ],
        // 租赁类型
        typeOfRental: '',
        directionOfHouse: '',
        //  价格区间
        priceRange: ['<1000', '1000-1500', '1500-2000', '2000-3000', '3000-5000', '5000-8000', '>8000'],
        //  用户选择的价格区间
        priceSelected: ''
      }
    }
  },
  components: {
    Footer
  },
  watch: {
    /*
    * 监听筛选选项的变化,更新房屋数据
    */
    RentalOptions: {
      handler (newValue, oldValue) {
        clearTimeout(this.timer)

        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(255, 255, 255, 0.7)'
        })

        this.timer = setTimeout(async () => {
          const that = this
          //  选择的城市
          const cityPyName = newValue.selectCityName.cityPyName
          // const cityCnName = newValue.selectCityName.cityCnName
          // const cityId = newValue.selectCityName.cityId

          //  选择的区域或者地铁
          let regionParams = 'a' + newValue.regionId
          let subwayStationId = 'b' + newValue.subwayStationId

          const priceSelected = newValue.priceSelected
          //  价格区间
          let priceRangeParams = 'r'
          if (priceSelected.indexOf('<1000') !== -1) {
            priceRangeParams += '1'
          }
          if (priceSelected.indexOf('1000-1500') !== -1) {
            priceRangeParams += '2'
          }
          if (priceSelected.indexOf('1500-2000') !== -1) {
            priceRangeParams += '3'
          }
          if (priceSelected.indexOf('2000-3000') !== -1) {
            priceRangeParams += '4'
          }
          if (priceSelected.indexOf('3000-5000') !== -1) {
            priceRangeParams += '5'
          }
          if (priceSelected.indexOf('5000-8000') !== -1) {
            priceRangeParams += '6'
          }
          if (priceSelected.indexOf('>8000') !== -1) {
            priceRangeParams += '7'
          }
          // 租赁类型
          let typeOfRental = ''
          switch (newValue.typeOfRental) {
            case '整租':
              typeOfRental = 'z1'
              break
            case '合租':
              typeOfRental = 'z2'
              break
          }
          // 房屋朝向
          let direction = ''
          switch (newValue.directionOfHouse) {
            case '东':
              direction = 'g1'
              break
            case '南':
              direction = 'g2'
              break
            case '西':
              direction = 'g3'
              break
            case '北':
              direction = 'g4'
              break
          }

          const paramsList = []
          if (typeOfRental !== 'z12' && typeOfRental !== '') {
            paramsList.push(typeOfRental)
          }
          if (direction !== 'g1234' && direction !== '') {
            paramsList.push(direction)
          }
          if (priceRangeParams !== 'r1234567' && priceRangeParams !== 'r') {
            paramsList.push(priceRangeParams)
          }
          if (regionParams !== 'a' && regionParams !== '') {
            paramsList.push(regionParams)
          }
          if (subwayStationId !== 'b' && subwayStationId !== '') {
            paramsList.push(subwayStationId)
          }
          let params = ''
          for (let index = 0; index < paramsList.length; index++) {
            params += (paramsList[index] + '-')
          }
          // 去除最后一个多余的 -
          params = params.replace(/-$/, '')

          let getHouseUrl = ''

          if (params === '') {
            getHouseUrl = '/zufang/' + (cityPyName === '' ? 'sz' : cityPyName)
          } else {
            getHouseUrl = '/zufang/' +
                      '' + (cityPyName === '' ? 'sz' : cityPyName) + '' +
                      '' + '/p/' + params
          }
          // 如果用户有输入文字搜索,那么触发 城市 + 条件 + 关键词搜索
          if (!that.inputText.trim().match(/\s+/) && that.inputText !== '') {
            getHouseUrl += '/_search/' + this.inputText.trim()
          }

          that.$http.get(getHouseUrl, {
            params: {},
            headers: {
              'Content-Type': 'application/json'
            }
          })
            .then(function (response) {
              if (response) {
                if (response.status === 200) {
                  console.log(response.data.data)
                  that.zufangData = response.data.data
                  that.zufangDataLength = response.data.data.length
                } else {
                  that.$notify.error({
                    title: '请求失败',
                    message: response.data.errorMessage
                  })
                }
              }
            })
            .catch(function (error) {
              that.$notify.error({
                title: '服务器错误',
                message: error
              })
            })
            .then(function () {
              loading.close()
            })
        }, 800)
      },
      deep: true
    }
  },
  methods: {
    /*
    * 如果用户没有选择城市,那么就默认跳转到深圳
    */
    gotoSZ () {
      const that = this
      if (that.RentalOptions.selectCityName.cityPyName === '') {
        this.$router.push('/zufang/sz')
      }
    },

    /*
    * 用户修改城市
    */
    changeCity (cityName) {
      const that = this
      for (let city in that.cityNames) {
        // 先检查是否有此属性
        if (that.cityNames.hasOwnProperty(city)) {
          // 在城市列表中找到用户选择的城市,并将其他信息(拼音等)补全
          if (that.cityNames[city].cityPyName === cityName) {
            that.RentalOptions.selectCityName.cityCnName = that.cityNames[city].cityCnName
            that.RentalOptions.selectCityName.cityPyName = that.cityNames[city].cityPyName
            that.RentalOptions.selectCityName.cityId = that.cityNames[city].cityId
          }
        }
      }
      // console.log(that.RentalOptions.selectCityName.cityCnName)

      that.RentalOptions.areaOptions[0].value = `${that.RentalOptions.selectCityName.cityCnName}_${that.RentalOptions.selectCityName.cityPyName}_${that.RentalOptions.selectCityName.cityId}_区域`
      that.RentalOptions.areaOptions[1].value = `${that.RentalOptions.selectCityName.cityCnName}_${that.RentalOptions.selectCityName.cityPyName}_${that.RentalOptions.selectCityName.cityId}_地铁`
      this.searchByKeyWord()
    },

    /*
    * 用户修改当前区域,将修改后的区域传给筛选选项,引起其变化,从而运行 watch 中的函数更新房屋
    */
    changeCityRegion (cityRegion) {
      // 区域代码
      // console.log(cityRegion.length)
      // console.log('cityRegion：' + cityRegion)
      if (cityRegion.length === 2) {
        // console.log('cityRegion[1]：' + cityRegion[1])
        this.RentalOptions.regionId = cityRegion[1]
      } else if (cityRegion.length === 3) {
        // console.log('cityRegion[1]：' + cityRegion[2])
        this.RentalOptions.subwayStationId = cityRegion[2]
      }
    },

    /*
     * 用户点击搜索按钮(或者搜索框中回车/或者用户选择城市)
     */
    searchByKeyWord () {
      const that = this
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(255, 255, 255, 0.7)'
      })
      /*
                * 判断当前选择的城市,跳转到不同的路径
                * 如果当前选择为空,则跳转到深圳
                */
      that.$router.push('/zufang/' + that.RentalOptions.selectCityName.cityPyName)

      /*
       * 读取用户输入及其选择,并请求对应房源
       * 如果用户输入为空,则直接返回空
       */
      // console.log(!that.inputText.match(/\s+/) && that.inputText !== '')
      // console.log(that.inputText)
      if (!that.inputText.match(/\s+/) && that.inputText !== '') {
        that.$http.get('/zufang/' +
            (that.RentalOptions.selectCityName.cityPyName === '' ? 'sz' : that.RentalOptions.selectCityName.cityPyName) +
            '/_search/' +
            that.inputText,
        {
          params: {},
          headers: {
            'Content-Type': 'application/json'
          }
        })
          .then(function (response) {
            if (response) {
              if (response.status === 200) {
                console.log(response.data.data)
                that.zufangData = response.data.data
                that.zufangDataLength = response.data.data.length
              } else {
                throw response.data.errorMessage
              }
            }
          })
          .catch(function (error) {
            that.$notify.error({
              title: '服务器错误',
              message: error
            })
          })
          .then(function () {
            loading.close()
          })
      } else {
        loading.close()
        that.zufangData = []
        that.zufangDataLength = 0
      }
      // 如果动画还没被激发过,激发动画
      if (!that.searchResult) {
        // 将 searchResult 的值变为 true ,这将激发动画
        that.searchResult = true
      }
    }
  },
  created () {
    const that = this
    that.zufangDataLength = that.zufangData.length
    // 请求有房源的城市列表
    that.$http.get('/citys', {
      params: {},
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(function (response) {
        if (response) {
          // [{"cityCnName": "城市中文名称","cityId": "城市id"}]
          that.cityNames = response.data.data
        }
      })
      .catch(function (error) {
        console.log(error)
      })
      .then(function () {
      })
  },

  filters: {
    /*
    * 格式化时间
    */
    timeFormatter (e) {
      const year = e.getFullYear()
      const month = (e.getMonth() + 1).toString().padStart(2, '0')
      const day = e.getDate().toString().padStart(2, '0')
      return `${year}-${month}-${day}`
    }
  }
}
</script>
<style>
    /*去除 a 标签的下划线*/
    a {
        text-decoration: none;
    }

    a:link {
        text-decoration: none; /* 指正常的未被访问过的链接*/
    }

    a:visited {
        text-decoration: none; /*指已经访问过的链接*/
    }

    a:hover {
        text-decoration: none; /*指鼠标在链接*/
    }

    a:active {
        text-decoration: none; /* 指正在点的链接*/
    }

    .el-footer {
        /*color: #333;*/
        text-align: center;
        line-height: 60px;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        /*line-height: 3000px;*/
    }

    body > .el-container {
        margin-bottom: 40px;
    }

    /*搜索框左边选择模块的大小*/
    .input-with-select .el-input-group__prepend {
        background-color: #fff;
        width: 12%;
        font-size: smaller;
        text-align: left;
        font-family: DengXian_Reg, serif;
    }

    /*搜索框左边选择模块展开的页面宽度*/
    .selectOptions {
        width: 5%;
    }

    /*.time {*/
    /*  font-size: 13px;*/
    /*  !*color: #999;*!*/
    /*  color: red;*/
    /*}*/

    .bottom {
        margin-top: 13px;
        line-height: 12px;
    }

    .image {
        width: 100%;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .el-select {
        font-size: 16px;
        height: 55px;
    }

    .el-input__inner {
        /*border: 1px solid #dcdfe6;*/
        /*width: 300px;*/
        height: 55px !important;
    }

    /*右侧按钮的宽度*/
    .el-input-group__append {
        width: 10%;
    }

    .searchInput {
        margin-top: 10%;
        margin-left: 25%;
        margin-right: 25%;
        z-index: 990;
    }

    .searchResultInput {
        width: 50%;
        margin-top: 10%;
        margin-left: 25%;
        margin-right: 25%;

        height: 55px;
        z-index: 990;

        animation: moveSearchInput 1s;
        -moz-animation: moveSearchInput 1s;
        -webkit-animation: moveSearchInput 1s;
        -o-animation: moveSearchInput 1s;

        animation-fill-mode: forwards;
        /* Safari 和 Chrome */
        -webkit-animation-fill-mode: forwards;
        -moz-animation-fill-mode: forwards;
        -o-animation-fill-mode: forwards;
    }

    @keyframes moveSearchInput {
        0% {
            margin-top: 10%;
            /*margin-left: -24.5%;*/
            margin-left: 25%;
        }
        100% {
            margin-top: 2%;
            /*margin-left: -32%;*/
            margin-left: 25%;
        }
    }

    /* Firefox 和 Chrome */
    @-webkit-keyframes moveSearchInput {
        0% {
            margin-top: 10%;
            /*margin-left: 24.5%;*/
            margin-left: 25%;
        }
        100% {
            margin-top: 2%;
            /*margin-left: -32%;*/
            margin-left: 25%;
        }
    }

    body {
        z-index: 0;
        overflow: scroll;
    }

    /*筛选区域下划线的宽度*/
    .el-divider {
        margin: 1% 0;
    }
</style>



