<template>
  <div>
    <!--这个 div 存放两个 card -->
    <div
      style="width: 100%;height: 880px;background: url('../../static/img/login.jpg') no-repeat 0 0; background-size: cover">
      <div style="padding-left: 60%;padding-top: 8%">
        <!--登录 card-->
        <el-card class="box-card" v-if="this.$route.path==='/login'" shadow="always">
          <!--card 标题-->
          <div slot="header" class="clearfix">
            <span>欢迎登录</span>
          </div>
          <!--card 表单-->
          <div>
            <el-form :model="userLoginInfo" status-icon :rules="loginCheckRules" ref="userLoginInfo"
                     :validate-on-rule-change="false"
                     label-width="0">
              <el-form-item prop="name" style="width: 80%;padding-left: 10%">
                <el-input prefix-icon="el-icon-user" v-model.trim="userLoginInfo.name"
                          @focus="$refs['userLoginInfo'].clearValidate('name')"></el-input>
              </el-form-item>
              <el-form-item prop="password" style="width: 80%;padding-left: 10%">
                <el-input prefix-icon="el-icon-key" type="password" v-model.trim="userLoginInfo.password"
                          @focus="$refs['userLoginInfo'].clearValidate('password')"></el-input>
              </el-form-item>
              <el-form-item style="padding-bottom: 0">
                <el-button type="primary" size="medium" native-type="button" style="width: 80%"
                           @click="doLogin('userLoginInfo')">登陆
                </el-button>
                <p
                  style="padding-bottom: 0;margin-bottom: 0;font-size: 10px;width: 80%;padding-left: 10%;color:rgba(105,107,106,0.65);text-align: left;line-height: normal">
                  登陆即代表同意
                  <router-link to="/404" style="color: #409eff">《wehouse用户协议》</router-link>
                  及
                  <router-link to="/404" style="color: #409eff">《wehouse隐私政策》</router-link>
                </p>
                <p style="margin: 0">
                  <el-link style="color: #409eff" @click="toRegister('userLoginInfo')">没有账号?现在去注册</el-link>
                </p>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
        <!--注册 card-->
        <el-card class="box-card" v-if="this.$route.path==='/register'" shadow="always">
          <!--card 标题-->
          <div slot="header" class="clearfix">
            <span>新用户注册</span>
          </div>
          <!--card 表单-->
          <div>
            <el-form :model="userRegisterInfo" status-icon :rules="registerCheckRules" ref="userRegisterInfo"
                     :validate-on-rule-change="false"
                     label-width="0">
              <el-form-item prop="phoneNumber" style="width: 80%;padding-left: 10%">
                <el-input type="text" v-model.trim="userRegisterInfo.phoneNumber" autocomplete="off"
                          prefix-icon="el-icon-phone" placeholder="常用手机号"
                          @focus="$refs['userRegisterInfo'].clearValidate('phoneNumber')"></el-input>
              </el-form-item>
              <el-form-item prop="VerificationCode" style="width: 80%;padding-left: 10%">
                <el-input v-model.number.trim="userRegisterInfo.VerificationCode" maxlength="6" show-word-limit
                          clearable placeholder="短信验证码"
                          @focus="$refs['userRegisterInfo'].clearValidate('VerificationCode')">
                  <el-button slot="append" @click="getVerificationCode('userRegisterInfo')"
                             :disabled="disableGetVerificationCode">
                    {{leftTime}}
                  </el-button>
                </el-input>
              </el-form-item>
              <el-form-item prop="name" style="width: 80%;padding-left: 10%">
                <el-input type="text" v-model.trim="userRegisterInfo.name" autocomplete="off" placeholder="用户名"
                          @focus="$refs['userRegisterInfo'].clearValidate('name')"/>
              </el-form-item>
              <el-form-item prop="password" style="width: 80%;padding-left: 10%">
                <el-input type="password" v-model.trim="userRegisterInfo.password" autocomplete="off"
                          @focus="$refs['userRegisterInfo'].clearValidate('password')"
                          placeholder="请输入6位以上密码"/>
              </el-form-item>
              <el-form-item prop="checkPass" style="width: 80%;padding-left: 10%">
                <el-input type="password" v-model.trim="userRegisterInfo.checkPass" autocomplete="off"
                          @focus="$refs['userRegisterInfo'].clearValidate('checkPass')"
                          placeholder="请确认密码"/>
              </el-form-item>
              <el-form-item style="padding-bottom: 0">
                <el-button type="primary" size="medium" native-type="button" style="width: 80%"
                           @click="doRegister('userRegisterInfo')">注册
                </el-button>
                <p
                  style="padding-bottom: 0;margin-bottom: 0;font-size: 10px;width: 80%;padding-left: 10%;color:rgba(105,107,106,0.65);text-align: left;line-height: normal">
                  注册即代表同意
                  <router-link to="/404" style="color: #409eff">《wehouse用户协议》</router-link>
                  及
                  <router-link to="/404" style="color: #409eff">《wehouse隐私政策》</router-link>
                </p>
                <p style="margin: 0">
                  <el-link style="color: #409eff" @click="toLogin('userRegisterInfo')">已有账号？现在就登录</el-link>
                </p>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </div>

    </div>
    <Footer/>
  </div>
</template>

<script>
import Footer from './Footer'
import axios from 'axios'

// 手机号码验证规则
const validatePhoneNumber = (rule, value, callback) => {
  if (!value) {
    // this.disableGetVerificationCode = true
    return callback(new Error('手机号不能为空!'))
  }
  if (value.length < 11 || !/^(1[35789]\d{9})$/.test(value)) {
    // this.disableGetVerificationCode = true
    return callback(new Error('请输入正确的11位中国大陆手机号!'))
  } else {
    axios.post('/check/userphone', {
      userPhone: value
    }, {headers: {'Content-Type': 'application/json'}})
      .then(function (response) {
        if (response.data.errorCode === 200) {
          // that.disableGetVerificationCode = false
          callback()
        } else {
          return callback(new Error(response.data.errorMsg))
        }
      })
    // eslint-disable-next-line handle-callback-err
      .catch(function (error) {
        return callback(new Error('检测手机号是否可用错误,请稍后重试!'))
        // that.disableGetVerificationCode = false
        // callback()
      })
  }
}

export default {
  name: 'login',
  data () {
    const validateLoginName = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入用户名或手机号!'))
      } else {
        callback()
      }
    }
    const validateLoginPassword = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入登录密码!'))
      } else {
        callback()
      }
    }
    // 检验用户名是否已用,请求后端接口
    const validateName = (rule, value, callback) => {
      const that = this
      if (value === '') {
        return callback(new Error('请输入用户名!'))
      } else {
        that.$http.post('/check/username', {
          userName: value
        }, {headers: {'Content-Type': 'application/json'}})
          .then(function (response) {
            // console.log(response)
            if (response.data.errorCode === 200) {
              // that.disableGetVerificationCode = false
              callback()
            } else {
              return callback(new Error(response.data.errorMsg))
            }
          })
        // eslint-disable-next-line handle-callback-err
          .catch(function (error) {
            return callback(new Error('检测用户名是否可用错误,请稍后重试!'))
            // that.disableGetVerificationCode = false
            // callback()
          })
      }
    }
    // 密码验证规则
    const validatePassword = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入密码!'))
      } else {
        setTimeout(() => {
          if (value.length < 6 || value.search(/[a-z|A-Z]/) === -1 || value.search(/\d/) === -1 || value.search(/[@#$]/) === -1) {
            return callback(new Error('密码需六位以上,包含字母、数字及“@“、”#”、“$”中的一个'))
          }
          callback()
        }, 600)
      }
    }
    // 二次输入密码规则检验
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请再次输入密码!'))
      } else if (value !== this.userRegisterInfo.password) {
        return callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    // 验证码验证规则
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入六位验证码!'))
      } else if (value.toString().length !== 6) {
        return callback(new Error('请输入正确的六位验证码!'))
      } else {
        callback()
      }
    }
    return {
      // 登录信息
      userLoginInfo: {
        // 用户名或手机号
        name: '',
        // 密码
        password: ''
      },
      // 注册信息
      userRegisterInfo: {
        // 手机号
        phoneNumber: '',
        // 用户名
        name: '',
        // 密码
        password: '',
        // 再次输入密码
        checkPass: '',
        // 验证码
        VerificationCode: ''
      },
      leftTime: '获取验证码',
      userData: {
        // 用户头像url
        userHead: '',
        // 昵称
        userName: '',
        // 手机号
        userPhone: '',
        // 是否学生认证
        studentAuthentication: false
      },
      // 登录表单检验规则
      loginCheckRules: {
        name: [
          {validator: validateLoginName, trigger: 'blur'}
        ],
        password: [
          {validator: validateLoginPassword, trigger: 'blur'}
        ]
      },
      // 注册表单检验规则
      registerCheckRules: {
        // 不需要失焦验证,改为点击获取验证码按钮时进行验证
        phoneNumber: [],
        VerificationCode: [
          {validator: validateCode, trigger: 'blur'}
        ],
        name: [
          {validator: validateName, trigger: 'blur'}
        ],
        password: [
          {validator: validatePassword, trigger: 'blur'}
        ],
        checkPass: [
          {validator: validatePass2, trigger: 'blur'}
        ]
      },
      disableGetVerificationCode: false
    }
  },
  methods: {
    // 获取验证码
    getVerificationCode (formName) {
      const that = this
      // 当点击获取验证码后,为手机号添加验证规则
      that.registerCheckRules.phoneNumber = [{validator: validatePhoneNumber, trigger: 'blur'}]
      // 检验手机号是否填写正确
      that.$refs[formName].validateField('phoneNumber', function (errorMessage) {
        if (errorMessage === '') {
          that.leftTime = '发送中...'
          that.$http.get('/code/' + that.userRegisterInfo.phoneNumber)
            .then(function (response) {
              if (response.data.errorCode === 200) {
                // 禁用获取验证码按钮并开始倒计时,倒计时结束后停止禁用按钮
                that.disableGetVerificationCode = true
                let leftTime = 60
                const intervalID = setInterval(() => {
                  if (leftTime > 0) {
                    that.leftTime = '还剩' + leftTime-- + '秒'
                  } else {
                    that.leftTime = '获取验证码'
                    that.disableGetVerificationCode = false
                    clearInterval(intervalID)
                  }
                }, 1000)
              }
            })
            .catch(function (error) {
              console.log(error)
            })
            .then(function () {
              // always executed
            })
        } else {
          that.registerCheckRules.phoneNumber = []
        }
      })
    },
    /*
    * 执行登录请求
    */
    doLogin (formName) {
      const that = this
      that.$refs[formName].validate((valid, object) => {
        // console.log(object)
        if (valid) {
          // 如果审核通过,加载等待动画
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            background: 'rgba(245,245,245,0.7)'
          })
          // 发送登录请求
          that.$http.post('/login', {
            userName: that.userLoginInfo.name,
            userPassword: that.userLoginInfo.password
          }, {headers: {'Content-Type': 'application/json'}})
            .then(function (response) {
              // 结束动画
              loading.close()
              if (response !== undefined && response !== '') {
                console.log(response.data)
                // 登录成功,保存令牌到 cookie (有效期: 1 小时)
                that.$cookie.set('token', response.data.data.token, {expires: '1h'})
                that.$router.push('/refreshIndex')
              }
            })
            .catch(function (error) {
              // 结束动画
              loading.close()
              that.$notify.error({
                title: '登陆失败',
                message: error
              })
            })
        }
      })
    },
    /*
    * 执行注册请求
    */
    doRegister: function (formName) {
      const that = this
      // 当点击注册按钮后,为手机号添加验证
      that.registerCheckRules.phoneNumber = [{validator: validatePhoneNumber, trigger: 'blur'}]

      that.$refs[formName].validate((valid, object) => {
        /*
          * valid : 是否通过验证
          * object : 未通过验证的字段
          */
        // console.log(object)
        if (valid) {
          that.registerCheckRules.phoneNumber = []
          // 加载动画
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            background: 'rgba(245,245,245,0.7)'
          })
          // 向服务器提交请求
          that.$http.post('/register', {
            userPhone: that.userRegisterInfo.phoneNumber,
            userName: that.userRegisterInfo.name,
            userPassword: that.userRegisterInfo.password,
            code: that.userRegisterInfo.VerificationCode
          }, {headers: {'Content-Type': 'application/json'}})
            .then(function (response) {
              loading.close()
              // console.log(response)
              // console.log(response.status)
              // console.log(response.data)
              if (response.status === 200) {
                if (response.data.errorCode === 4003 || response.data.errorCode === 4004) {
                  // 注册出现问题
                  alert(response.data.errorMessage)
                } else if (response.data.errorCode === 200) {
                  // 注册通过,向后端发起登录请求,然后获取个人数据保存到本地,跳转首页

                  that.$notify.success({
                    title: '注册成功',
                    message: '注册成功,准备登录'
                  })

                  that.$http.post('/login', {
                    userName: that.userRegisterInfo.phoneNumber,
                    userPassword: that.userRegisterInfo.password
                  }, {headers: {'Content-Type': 'application/json'}})
                    .then(function (response) {
                      that.$notify.success({
                        title: '登录成功',
                        message: '登录成功,准备获取用户信息'
                      })

                      if (response.data.errorCode === 200) {
                        // 登录成功,保存令牌到 cookie (有效期: 1 小时)
                        that.$cookie.set('token', response.data.data.token, {expires: '1h'})
                        /*
                         * **********************************************************************
                         * 同时用 cookie 请求用户信息
                         */
                        that.$http.get('/i', {
                          params: {
                            Cookie: that.$cookie.get('token')
                          }
                        })
                          .then(function (response) {
                            that.$notify.success({
                              title: '获取用户信息成功',
                              message: '获取到用户信息'
                            })

                            if (response.data.errorCode === 200) {
                              // 将请求到的数据写到用户信息中
                              that.userData = response.data.data
                              // 将用户信息保存到 cookie,一小时过期
                              that.$cookie.set('userData', response.data.data, {expires: '1h'})
                            }
                          })
                          .catch(function (error) {
                            console.log(error)
                          })
                          .then(function () {})
                        /************************************************************************/
                      } else {
                        // 结束动画
                        loading.close()
                        alert(response.data.data.errorMessage)
                      }
                    })
                    .catch(function (error) {
                      // 结束动画
                      loading.close()
                      alert(error)
                      // console.log(error)
                    })

                  that.$router.push('/refreshIndex')
                }
              }
            })
            .catch(function (error) {
              loading.close()
              alert(error)
              // console.log(error)
            })
        } else {
          that.registerCheckRules.phoneNumber = []
          // console.log('请完善信息')
          return false
        }
      })
    },
    /*
    * 前往注册页面
    */
    toRegister (formName) {
      const that = this
      that.$refs[formName].clearValidate()
      that.$router.push('/register')
    },
    /*
    * 前往登录页面
    */
    toLogin (formName) {
      const that = this
      that.$refs[formName].clearValidate()
      that.$router.push('/login')
    }
  },
  created () {
  },
  components: {Footer}
}
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 0;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  /*card 的宽度*/
  .box-card {
    width: 55%;
  }
</style>



