import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/Home'
// import Home from '../views/Home'
import index from '../views/index'
import test from '../views/test'

Vue.use(Router)

// 在这里配置路径
const routes = [
  // 测试页面
  {
    path: '/test',
    name: 'test',
    component: test,
    meta: {
      title: '测试'
    }
  },
  /*
      * 首页的四大组件：logo|首页、关于、登录、注册
      */
  {
    path: '/',
    name: 'Home_/',
    component: index,
    meta: {
      title: 'wehouse'
    }
  },
  {
    path: '/index.html',
    name: 'Home_index',
    component: index,
    meta: {
      title: 'wehouse'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: index,
    meta: {
      title: '登录wehouse'
    }
  },
  {
    path: '/register',
    name: 'register',
    component: index,
    meta: {
      title: '注册wehouse'
    }
  },
  {
    path: '/about',
    name: 'about',
    component: index,
    meta: {
      title: '关于wehouse'
    }
  },
  {
    path: '/refreshIndex',
    name: 'refreshIndex',
    component: () => import(/* webpackChunkName: "i" */ '../utils/refreshIndex'),
    meta: {
      title: ''
    }
  },
  {
    path: '/zufang/:city/housedetail/:id',
    name: 'housedetail',
    component: () => import(/* webpackChunkName: "i" */ '../views/HouseDetail'),
    meta: {
      title: 'wehouse | 房屋详情'
    }
  },
  {
    path: '/map',
    name: 'searchByMap',
    component: () => import(/* webpackChunkName: "i" */ '../views/GMap'),
    meta: {
      title: 'wehouse | 地图找房'
    }
  },
  {
    path: '/zufang/:city',
    name: 'zuFang',
    component: () => import('../views/index'),
    meta: {
      title: ''
    }
  },
  {
    path: '/addHouses',
    name: 'addHouses',
    component: () => import('../views/addHouses'),
    meta: {
      title: '添加房屋'
    }
  },

  {
    path: '/user/:userPhone/:userName',
    name: 'user',
    component: () => import('../views/userData.vue'),
    meta: {
      title: ''
    }
  },
  { /* Not Found 路由，必须是最后一个路由 */
    path: '*',
    component: () => import(/* webpackChunkName: "404" */ '../views/404.vue'),
    meta: {
      title: '找不到页面'
    }
  }
]

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 动态设置页面标题
router.beforeEach((to, from, next) => {
  // 如果前往个人页面,那么把标题设置为用户名
  let regUserData = new RegExp('/user/\\d{11}/.*', 'g')
  regUserData.lastIndex = 0

  let regZuFangCity = new RegExp('/zufang/.*', 'g')
  regZuFangCity.lastIndex = 0

  if (regUserData.test(to.path.toString())) {
    // 个人页面
    regUserData.lastIndex = 0
    window.document.title = 'wehouse | ' + to.path.split('/')[3]
  } else if (regZuFangCity.test(to.path.toString())) {
    // 某城市租房页面
    regZuFangCity.lastIndex = 0
    let cityName = ''
    switch (to.path.split('/')[2]) {
      case 'sz':
        cityName = '深圳'
        break
      case 'gz':
        cityName = '广州'
        break
    }
    window.document.title = 'wehouse | ' + cityName
  } else {
    // 重置正则
    regUserData.lastIndex = 0
    regZuFangCity.lastIndex = 0
    window.document.title = to.meta.title
  }
  next()
})

export default router



